import QQMapWX from "@/common/js/qqmap-wx-jssdk.js"
export default {
	data() {
		return {
			id:'',
			isSend:true,
			desc:'',
			qqMapSdk:null,
			markers:[],
			lable:{
				content:''
			},
			dataObj: {
				groupName: "", //公司名称
				gostName: "", //职位名称
				money: "" ,//薪资
				desc: "" ,//职位职责
				address: "", //位置
				lng: "40.01", //维度
				lat: "", //经度
				collte: 0 ,//  收藏   0  否   1  是
				send: '',//  投递   0  否   1  是
				
			}
		}
	},
	onLoad(options) {
		this.id=options.id;
		// this.qqMapSdk = new QQMapWX({
		// 	key: 'GQZBZ-3CECD-BP443-PBQEY-4R3M6-QMFBO'
		// });
		this.LoadData();	
	},
	onShow() {
		if(this.$wechat.isWechat()){
		    this.$wechat.wxShare(); 
		} 
	},
	methods: {
		markertap(e){
			console.log(e)
		},
		changeCollte(){
			let text='';
			let parmas={
				id:this.id,
				uid:getApp().globalData.uid
			}
			this._reqw.ipost(parmas, 'collectionOrCancel').then(res => {
				res.result == 0 ? (this.dataObj.collte==0?(this.dataObj.collte=1,text="收藏"):(this.dataObj.collte=0,text='取消'),this.$api.tip(`${text}成功!`)) : this.$api.tip(res.resultNote)
			}).catch(err => {})
		},
		LoadData() {
			let parmas = {
				id: this.id,
				uid: getApp().globalData.uid
			}
			this._reqw.ipost(parmas, 'productDetail').then(res => {
				res.result == 0 ? (this.dataObj = res,this.markers[0].latitude=res.lat,this.markers[0].longitude=res.lng, this.desc=this.$api.formText(res.desc)): this.$api.tip(res.resultNote)
			}).catch(err => {})
		},
		gotoGroup() {
			this.$api.navTo(`/pages/groupInfo?id=${this.dataObj.groupId}&name=${this.dataObj.groupName}`)
		},
		sendBlo(){
			if(getApp().globalData.uid==''){
				this.$api.tip('请先登录')
			}else{
				let parmas={
					id:this.id,
					uid:getApp().globalData.uid
				}
				this._reqw.ipost(parmas, 'sendbiographical').then(res => {
					res.result == 0 ? (this.$api.tip('投递成功!'),this.isSend=false): this.$api.tip(res.resultNote)
				}).catch(err => {})
			}
		}
	}
}
